<template>
  <div>
    <div class="header login"
         v-if="user">
      <div class="userInfo">
        <div class="left">
          <van-image class="avatar"
                     round
                     :src="userInfo.photo"
                     fit="cover" />
          <span class="name">{{userInfo.name}}</span>
        </div>
        <div class="right">
          <van-button size="mini"
                      to="/user/editinfo"
                      round>编辑资料</van-button>
        </div>
      </div>
      <div class="dataStatistics">
        <div class="data-item">
          <span class="count">{{userInfo.art_count}}</span>
          <span class="text">头条</span>
        </div>
        <div class="data-item">
          <span class="count">{{userInfo.follow_count}}</span>
          <span class="text">关注</span>
        </div>
        <div class="data-item">
          <span class="count">{{userInfo.fans_count}}</span>
          <span class="text">粉丝</span>
        </div>
        <div class="data-item">
          <span class="count">{{userInfo.like_count}}</span>
          <span class="text">获赞</span>
        </div>
      </div>
    </div>
    <div class="header not-login"
         v-else>
      <div class="login-btn"
           @click="$router.push('/login')">
        <img src="~@/assets/mobile.png"
             alt="">
        <span>登录 / 注册</span>
      </div>
    </div>
    <!-- 导航 -->
    <van-grid :column-num="2"
              class="grid-nav"
              :border='false'
              clickable>
      <van-grid-item @click="toCollect(i=0)">
        <i slot="icon"
           class="iconshoucang iconfont"></i>
        <span slot="text"
              class="name1">收 藏</span>
      </van-grid-item>
      <van-grid-item @click="toCollect(i=1)">
        <i slot="icon"
           class="iconlishi iconfont"></i>
        <span slot="text"
              class="name1">历 史</span>
      </van-grid-item>
    </van-grid>
    <van-cell title="消息通知"
              is-link />
    <van-cell title="小智同学"
              to="/user/robot"
              is-link />
    <van-cell title="退出登录"
              clickable
              class="logout"
              v-if="user"
              @click="logout" />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getUserInfo } from '@/api/user'
export default {
  data () {
    return {
      // 用户自己的信息
      userInfo: {}
    }
  },
  created () {
    // 先判断登陆后才发起请求
    if (this.user) {
      this.loadUserInfo()
    }
  },
  activated () {
    if (this.user) {
      this.loadUserInfo()
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    logout () {
      this.$dialog.confirm({
        title: '确定退出？'
      })
        .then(() => {
          // on confirm
          this.$store.commit('saveToken', null)
        })
        .catch(() => {
          // on cancel
        })
    },
    async loadUserInfo () {
      try {
        const { data: res } = await getUserInfo()
        // console.log(res)
        this.userInfo = res.data
      } catch (err) {
        this.$toast('获取数据失败，请稍后重试')
      }
    },
    toCollect (i) {
      this.$router.push(`/user/collectHistory/${i}`)
    }
  }
}
</script>

<style lang="scss" scoped>
.header {
  height: 361px;
  background: url("~@/assets/banner.png");
  background-size: cover;
}
.not-login {
  display: flex;
  justify-content: center;
  align-items: center;
  .login-btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      width: 132px;
      height: 132px;
    }
    span {
      margin-top: 15px;
      font-size: 28px;
      color: #fff;
    }
  }
}
.login {
  .userInfo {
    height: 231px;
    padding: 76px 32px 23px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .left {
      display: flex;
      align-items: center;
      .avatar {
        width: 132px;
        height: 132px;
        margin-right: 23px;
        border: 2px solid #fff;
      }
      .name {
        font-size: 30px;
        color: #fff;
      }
    }
    .right {
      .van-button {
        position: relative;
        top: -14px;
        width: 115px;
        height: 32px;
        line-height: 32px;
        font-size: 20px;
        color: #666;
      }
    }
  }
  .dataStatistics {
    display: flex;
    justify-content: space-around;
    padding: 0 32px;
    .data-item {
      height: 130px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: #fff;
      .count {
        font-size: 36px;
      }
      .text {
        font-size: 23px;
      }
    }
  }
}
.grid-nav {
  margin-bottom: 18px;
  .van-grid-item {
    &:nth-child(1)::before {
      content: "";
      display: block;
      width: 1px;
      height: 102px;
      background: #edeff3;
      position: absolute;
      top: 30px;
      right: 0;
      z-index: 99;
    }
  }
  .iconfont {
    color: #eb5253;
    font-size: 45px;
  }
  .iconlishi {
    color: #ff9d1d;
  }
  .name1 {
    font-size: 28px;
    margin-top: 16px;
  }
}
.logout {
  text-align: center;
  font-size: 30px;
  color: #d86262;
  margin-top: 10px;
}
</style>
